
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import Drive from '@/tool/classFactory/drive.js';
import {getCookie} from '@/tool/common.js';
export default {
components: {
Group,
Cell,
Toast
},
data () {
return {
avartar: 'https://wx.qlogo.cn/mmopen/vi_32/DYAIOgq83eoLKnzzlSdMqQzIPhdxIZ2JyROWeajaMMib8Lq8PfmDnic3ou9oU7jJ8U21Esu4OQibRr04ccYy8THXA/132',
password:'',
params: {
'password': '',
'sessionId':''
},
showPositionValue: false,
middle: 'middle',
msg:'msg',
params03: {
'sessionId':''
},
params02:{
'path':'/stulogin'
},
params04:{
'sessionId':''
},
params05:{
'sessionId':''
},
stuMsg:{
"avatar":'',
"nikename":''
}
}
},
created () {
this.params05.sessionId = this.params.sessionId = this.params04.sessionId = this.params03.sessionId = getCookie('sessionId')
console.log(this.params04.sessionId)
this.judgeBindCard()



},
methods: {
judgePassword(){
const that = this;
Drive.prototype.judgePassword(that.params03).then(res => {
let list = res.data.data || [];
console.log(res.data.status)
if(res.data.status == 2000000){
that.$router.push('../index')
}else {
that.login = false
}
});
},
getUserMsg(){
const that = this;
Drive.prototype.getUserMsg(that.params05).then(res => {
let list = res.data.data || [];
that.stuMsg = list
console.log(list)
});
},
register(){
const that = this;
Drive.prototype.register(that.params02).then(res => {
console.log(res)
let list = res.data.data || {};
console.log(list)
that.stuMsg = list

console.log(res.data.status)
});
},
simulateLogin () {
const that = this;
Drive.prototype.simulateLogin(that.params).then(res => {

console.log(res)
if(res.data.status == 2000000){
that.$router.push('../index')

}else {
that.showPositionValue = true
that.msg = res.data.message
}
});
},
judgeBindCard (){
const that = this;
Drive.prototype.judgeBindCard(that.params04).then(res => {
console.log('hcj')
console.log(res)
if(res.data.status == 2000000){
that.getUserMsg()
that.$router.push('../index')

}else if(res.data.status == 5000002) {
that.showPositionValue = true
that.msg = '请重新登录'
}else {
that.getUserMsg()
that.showPositionValue = true
that.msg = res.data.message

}
});
},
bindCard (){
const that = this;
Drive.prototype.bindCard(that.params).then(res => {
console.log(res)
if(res.data.status == 2000000){
that.$router.push('../index')

}else if(res.data.status == 5000002) {
that.showPositionValue = true
that.msg = '请重新登录'
} else {
that.showPositionValue = true
that.msg = res.data.message
}
});
},
setCookie: function (cname, cvalue, exdays) {
var d = new Date()
d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
var expires = 'expires=' + d.toUTCString()
console.info(cname + "=" + cvalue + "; " + expires)
document.cookie = cname + "=" + cvalue + "; " + expires
console.info(document.cookie);
},
onBindTap (){
if(this.params05.sessionId){
this.bindCard()
}else {
this.showPositionValue = true
this.msg='请先微信登录'
}


},
onRegisterTap(){

var url = 'http://jindou360.com:8080/jkrj/api/wechat/auth/register.jhtml?path=' + '/stulogin'
window.location.href = url
}
}
}



